.my-app {
  text-align: center;
  }
  
  .my-app-logo {
  height: 40vmin;
  pointer-events: none;
  }
  
  @media (prefers-reduced-motion: no-preference) {
  .my-app-logo {
  animation: my-app-logo-spin infinite 20s linear;
  }
  }
  
  .my-app-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  }
  
  .my-app-link {
  color: #61dafb;
  }
  
  @keyframes my-app-logo-spin {
  from {
  transform: rotate(0deg);
  }
  to {
  transform: rotate(360deg);
  }
  }
  
